  return `<h2 id="using-git-in-files" data-svelte-h="svelte-s1rz37">Using Git in Files</h2> <p data-svelte-h="svelte-o5pl6n">The Git integration in Files is designed to make it easier to work with your project. The integration displays useful information about the current branch and commit details on the Status Bar, support for creating and switching branches, and Git columns in the Details layout.</p> <h3 id="connecting-to-github" data-svelte-h="svelte-h5z7x2">Connecting to Github</h3> <p data-svelte-h="svelte-13ei4xm">Connecting to Github allows you to push and sync changes with remote repositories. Files will prompt and display instructions when authorization is required.</p> <h3 id="creating-and-switching-branches" data-svelte-h="svelte-1tmk0sx">Creating and switching branches</h3> <p data-svelte-h="svelte-1q6kkxv">The current branch name will displayed on the Status Bar. To create a new branch, click the branch name to open the Branches flyout and click the “Create” button. To switch branches, open the “Branches” flyout and select the branch you want to checkout.</p> <h3 id="push-pull-and-sync-changes-from-a-remote-repository" data-svelte-h="svelte-nxo3x8">Push, pull, and sync changes from a remote repository</h3> <p data-svelte-h="svelte-1oif4s1">The number of local and remote commits will be displayed on the Status Bar. To push or pull commits from a remote repo, click on the commit count and select the push or pull option. Syncing with the remote repo will update the commit number on the status bar without taking any actions.</p> <h3 id="git-columns-in-the-details-layout" data-svelte-h="svelte-1n1flor">Git columns in the Details layout</h3> <p data-svelte-h="svelte-26ynmx">The Details layout will display additional columns for Git status, last commit, commit hash, and commit author. These columns can be toggled by right-clicking the column headers.</p> <h3 id="git-details-in-the-details-pane" data-svelte-h="svelte-1d5lhcl">Git details in the Details pane</h3> <p data-svelte-h="svelte-n2d6rq">The Details Pane will display the repository name and current checked out branch in the Details Pane.</p> <h3 id="opening-a-git-repository-in-vs-code-or-your-ide-of-choice" data-svelte-h="svelte-k5p4x8">Opening a Git repository in VS Code (or your IDE of choice)</h3> <p data-svelte-h="svelte-15xfjzf">When browsing a Git repository, the Status Bar will show an option to open the current folder in your selected IDE.</p>`;
