  return `<p data-svelte-h="svelte-20ts3h">We’re excited to announce that version 2.5.10 is now available. If you already have Files, you’ll get a notification in the app to update. Otherwise, you can download Files for free from our <a href="/download/">download page</a>.</p> <p data-svelte-h="svelte-ipev07"><strong>TL;DR:</strong> This update adds Git columns to the details layout, an open VS Code button in Git repos, and improvements to the details pane. Continue reading to learn more about these changes.</p> <h2 id="whats-new-in-v2510" data-svelte-h="svelte-dtj0es">What’s new in v2.5.10</h2> <h3 id="git-columns-in-the-details-layout" data-svelte-h="svelte-vchp9n">Git columns in the details layout</h3> <p data-svelte-h="svelte-aybwl5">Opening a Git repo in the details layout will now display additional columns with information about the Git status, last commit, commit hash, and commit author. These columns can be toggled by right clicking the column headers.</p> <figure data-svelte-h="svelte-1xgi710"><img src="/blog-resources/files2-5-10/GitColumns.png" alt="Git columns in the details layout."></figure> <h3 id="open-git-repo-in-vs-code" data-svelte-h="svelte-yn13p1">Open Git repo in VS Code</h3> <p data-svelte-h="svelte-kma88e">When viewing a Git repo, the status bar will now show a button to open the folder in VS Code. Support for additional applications is being considered.</p> <figure data-svelte-h="svelte-ux76tw"><img src="/blog-resources/files2-5-10/OpenVSCode.png" alt="Details pane."></figure> <h3 id="show-details-about-the-current-directory-when-no-items-are-selected" data-svelte-h="svelte-1btmais">Show details about the current directory when no items are selected</h3> <p data-svelte-h="svelte-9j7c5k">The details pane will show information about the current directory when no items are currently selected. In the future, we have plans to show storage details when viewing drives.</p> <figure data-svelte-h="svelte-qlhd7m"><img src="/blog-resources/files2-5-10/DetailsPane.png" alt="Details pane."></figure> <h2 id="changes-and-improvements" data-svelte-h="svelte-zx5w86">Changes and Improvements</h2> <h3 id="removed-the-bundles-widget" data-svelte-h="svelte-121j5y7">[Removed the Bundles widget]</h3> <p data-svelte-h="svelte-1usii4x">Earlier this year, we announced plans to replace the Bundles widget with the Tags widget. Starting in v2.5.10, the Bundles widget is no longer available.</p> <h3 id="auto-populate-version-information-when-submitting-feedback" data-svelte-h="svelte-8j1pzf">[Auto-Populate version information when submitting feedback]</h3> <p data-svelte-h="svelte-5g5d1x">Submitting feedback from the About page will automatically fill out the version information on the GitHub issue.</p> <h3 id="separated-the-archive-options-in-the-right-click-menu" data-svelte-h="svelte-1iw20u9">[Separated the archive options in the right-click menu]</h3> <p data-svelte-h="svelte-10ituw3">The menu items for compressing and extracting files has been separated into two separate menus. Previously the items were grouped together under “Archive”.</p> <h3 id="added-an-option-to-hide-the-send-to-menu" data-svelte-h="svelte-1tyzcx0">[Added an option to hide the Send To menu]</h3> <p data-svelte-h="svelte-15xhnqr">Users looking to customize the right-click menu can now hide the Send To menu.</p> <h2 id="bug-fixes" data-svelte-h="svelte-raeaxr">Bug Fixes</h2> <ul data-svelte-h="svelte-13hm3c9"><li>Fixed crash that would sometimes occur when switching away from Git repos</li> <li>Fixed crash when that would occur when the Git branch tip was null</li> <li>Fixed issue where folder sizes weren’t shown in Recycle Bin</li> <li>Fixed issue where column layout items remained selected on certain startup settings</li> <li>Fixed issue where the scrollbar wasn’t visible when reordering pinned favorites</li> <li>Fixed issue where tooltips in the details layout was displaying the wrong data</li> <li>Fixed issue where archive files were counted as folders</li> <li>Fixed issue where navigating away from a search wouldn’t show the folders items</li> <li>Fixed issue where item names in the Tags widget didn’t have ellipsis</li> <li>Fixed tab navigation when middle-clicking a sidebar item</li> <li>Fixed issue where middle clicking selected folders would cause a crash</li> <li>Fixed issue where the icon was missing from the “new shortcut” option in the right-click menu</li> <li>Fixed an issue where the right-click menu would sometimes freeze the app</li></ul> <h3 id="conclusion" data-svelte-h="svelte-toxqw4">Conclusion</h3> <p data-svelte-h="svelte-1r54eb6">We hope you enjoy using Files as much as we enjoyed making it.</p> <p data-svelte-h="svelte-19u3577">As always, we appreciate your feedback and suggestions on how we can make Files better for you. You can reach us through our GitHub issues page: <a href="https://github.com/files-community/Files/issues" rel="nofollow">https://github.com/files-community/Files/issues</a>.</p> <p data-svelte-h="svelte-1q4h339">Thank you for using Files! 😊</p> <hr> <p data-svelte-h="svelte-1r1a9qf">Download Files from our <a href="/download/">download page</a>.</p>`;
