import { c as create_ssr_component } from "../../../../../chunks/ssr.js";
const metadata = { "title": "Translating Files" };
const Page = create_ssr_component(($$result, $$props, $$bindings, slots) => {
  return `<p data-svelte-h="svelte-15u9neh">Translations for Files are done through the popular cloud based platform Crowdin. Anyone with a Crowdin account can translate strings and help improve existing strings.</p> <h2 id="getting-started-with-crowdin" data-svelte-h="svelte-1gpbxm3">Getting started with Crowdin</h2> <ul data-svelte-h="svelte-16uxm7p"><li>A suitable web browser</li> <li>An account with <a href="https://accounts.crowdin.com/register" rel="nofollow">Crowdin</a></li></ul> <h2 id="proofreading" data-svelte-h="svelte-13dw50e">Proofreading</h2> <p data-svelte-h="svelte-1xo3ki5">Proofreading and approving strings is done by active community members; if you would like to proofread your language please start a discussion on Crowdin and we’ll review your request at the earliest convenience.</p> <h2 id="screenshots" data-svelte-h="svelte-g62108">Screenshots</h2> <p data-svelte-h="svelte-h2ckx7">Where possible, screenshots have been added to provide more context on how strings are being used. Please note, Files is actively updated and it’s possible that some screenshots are out of date.</p> <h2 id="syncing-changes-with-the-files-app" data-svelte-h="svelte-1j0h7ob">Syncing changes with the Files app</h2> <p data-svelte-h="svelte-jrtmzy">Translations are usually synced with the main Files project shortly before releasing an update to the stable branch.</p> <h2 id="placeholders" data-svelte-h="svelte-1jn0ft6">Placeholders</h2> <p data-svelte-h="svelte-fu02ml">Numbers that are surrounded by curly brackets are the placeholder where actual values are set at runtime. Translators may have to consider under what context string are used, and so we sometimes add comments about at which sentences are basically shown and what they are for as long as possible.</p> <p data-svelte-h="svelte-1gkqha7">For example:</p> <pre class="language-undefined"><!-- HTML_TAG_START -->${`<code class="language-undefined">Are you sure you want to delete &#123;0&#125; from &#123;1&#125;?</code>`}<!-- HTML_TAG_END --></pre> <p data-svelte-h="svelte-9wtrzz">In this case, the first placeholder is for the file name that the user wants to delete and the second one is for the folder name that user wants to delete a file from.</p> <h2 id="icu-message-syntax" data-svelte-h="svelte-12ye3e">ICU message syntax</h2> <p data-svelte-h="svelte-lqt47i">You may see some strings that have programmatic syntax.
It’s called <a href="https://support.crowdin.com/icu-message-syntax" rel="nofollow">ICU message syntax</a>, which is the standard syntax advanced by Unicode (and the parallel ISO 10646 standard) for efficiently processing text in any language and for maintaining text data integrity.</p> <h3 id="pluralization" data-svelte-h="svelte-jj7px8">Pluralization</h3> <p data-svelte-h="svelte-kbj6j2">Plural type is used to handle plural category variations, as each language has its own set of plural categories (for example, English has “one” and “other”, while Ukrainian has “one”, “few”, “many”, and “other”).</p> <p data-svelte-h="svelte-1motco3">The following short tags are used to determine the plural categories:</p> <ul data-svelte-h="svelte-12lqryu"><li>zero</li> <li>one (singular)</li> <li>two (dual)</li> <li>few (paucal)</li> <li>many (also used for fractions if they have a separate class)</li> <li>other (required: general plural form — also used if the language only has a single form)</li></ul> <p data-svelte-h="svelte-1htgh0x">In the Crowdin editor, you don’t have to manually add or delete plural categories to the translations you are making. Just click <code>Copy Source</code>, and the source string will be copied to the translation field with the number of plural categories right for the current target language.</p> <img src="/docs-resources/Crowdin-EditorCommandButtons.png" alt="Editor command buttons"> <p data-svelte-h="svelte-1cl2ezj">For a complete list of languages with Language Plural Rules, visit <a href="https://www.unicode.org/cldr/charts/45/supplemental/language_plural_rules.html" rel="nofollow">Unicode web page</a>.</p> <p data-svelte-h="svelte-ujch5x">Please do care about:</p> <ul data-svelte-h="svelte-1kxe7hi"><li><code>other</code> tag must always be defined in any languages.</li> <li>If there are tags that have the same value as the <code>other</code> tag, they can be omitted.</li> <li>In the case of a specific translation for a learned value, you can use <code>=NUMBER {TEXT}</code>.You should only use this for values that are not specified using predefined tags in source language (<code>en-US</code>). This is most commonly used to define for the value <code>0</code>.</li></ul> <p data-svelte-h="svelte-141ncqe">For example: there are no plurals in Japanese, so the <code>other</code> tag has to be defined despite the <code>one</code> and <code>other</code> tags having the same value in Japanese.</p> <h3 id="number" data-svelte-h="svelte-fq4xb8">Number</h3> <p data-svelte-h="svelte-117q123">The purpose of the number type is to display different number values such as percentage, currency, and decimal numbers independently from the locale conventions for those. This enables adjustment of the message output to the number formats used in different locales.</p> <p data-svelte-h="svelte-w6e49r">The placeholder <code>{0, number}</code> only specifies the resulting number format. This is <em>never</em> translated and must always be kept in the same form, but obviously can be moved to positions that require translation.</p> <p data-svelte-h="svelte-3e7re0">Please mind:</p> <ul data-svelte-h="svelte-1grlna6"><li>The placeholder <code>#</code> indicates where the value will be inserted. This allows you to define a future position according to your translation.</li></ul> <h3 id="remarks" data-svelte-h="svelte-118702a">Remarks</h3> <p data-svelte-h="svelte-10n7ym4">If you’re not sure which tags should be added, you can click the <code>i</code> button to see available tags. You can also click the ‘generate skelton’ button to see a preview.</p> <img src="/docs-resources/Crowdin-PreviewGenTip.png" alt="Generate skeleton to see preview"> <img src="/docs-resources/Crowdin-AvailablePluralTypesTip.png" alt="See what kinds of plurals are permitted in this language"> <p data-svelte-h="svelte-1j3pgtv">Also, Crowdin shows an error message if your translation is incorrectly formatted or translated.</p> <h2 id="discussions" data-svelte-h="svelte-r0zb1w">Discussions</h2> <p data-svelte-h="svelte-c894m6">If you have conprehensive questions about Files translation, please use <a href="https://crowdin.com/project/files-app/discussions" rel="nofollow">Crowdin Discussions</a>. We require that all discussions via Crowdin are <strong>in English</strong>; if otherwise they will be closed at our discretion.</p> <p data-svelte-h="svelte-1sef5rx">For questions regarding a single translation please use the Comments area of each translation in Crowdin Editor. You can mark your comment as an Issue and choose a reason for why you want bring your issue to us. Once your discussion is marked as an issue by an administrator, your discussion will be highlighted in red so others can chip in.</p> <h2 id="localizing-the-code" data-svelte-h="svelte-1m964r6">Localizing the code</h2> <p data-svelte-h="svelte-p77in9">We have our own extension to handle ICU message syntax.
<code>public static Strings.ToLocalized(this source, param parameters);</code></p> <pre class="language-cs"><!-- HTML_TAG_START -->${`<code class="language-cs"><span class="token string">"ItemsCount"</span><span class="token punctuation">.</span><span class="token function">ToLocalized</span><span class="token punctuation">(</span>itemsCount<span class="token punctuation">)</span><span class="token punctuation">;</span></code>`}<!-- HTML_TAG_END --></pre> <pre class="language-cs"><!-- HTML_TAG_START -->${`<code class="language-cs"><span class="token class-name"><span class="token keyword">var</span></span> localized <span class="token operator">=</span> <span class="token string">"DeleteConfirmation"</span><span class="token punctuation">.</span><span class="token function">ToLocalized</span><span class="token punctuation">(</span>fileName<span class="token punctuation">,</span> parentFolderName<span class="token punctuation">)</span><span class="token punctuation">;</span></code>`}<!-- HTML_TAG_END --></pre>`;
});
export {
  Page as default,
  metadata
};
