  return `<p data-svelte-h="svelte-1xbkc3c">Files comes equipped with robust support for connecting to remote FTP and FTPS servers. This feature allows you to seamlessly access any compatible server, explore its directory structure, and perform tasks such as editing, renaming, and deleting files or folders as needed.</p> <h2 id="establishing-a-connection-with-ftp-servers" data-svelte-h="svelte-1uesv4l">Establishing a connection with FTP servers</h2> <p data-svelte-h="svelte-1tr2p3m">To connect to remote servers, use the address bar by entering specific paths. Here’s how:</p> <h3 id="ftp" data-svelte-h="svelte-611nac">FTP</h3> <p data-svelte-h="svelte-1dqhwlm">Enter the following in the address bar: <code>ftp://&lt;server_address&gt;:&lt;port&gt;</code>. For example, if your server address is <code>ftp.example.com</code> and you’re using the default port, enter <code>ftp://ftp.example.com:21</code>. If you don’t specify a port, it defaults to port 21.</p> <h3 id="ftps" data-svelte-h="svelte-3nyi30">FTPS</h3> <p data-svelte-h="svelte-1rum4uh">For a secure connection using FTPS, use this format: <code>ftps://&lt;server_address&gt;:&lt;port&gt;</code>. For example, if your server address is <code>ftps.example.com</code> and you’re using the default port, enter <code>ftps://ftps.example.com:990</code>. If you don’t specify a port, it defaults to port 990.</p> <p data-svelte-h="svelte-jq8g62">When required, Files will prompt you to enter your credentials (username and password) to log in securely.</p> <h2 id="disconnecting-from-ftp-servers" data-svelte-h="svelte-13c603l">Disconnecting from FTP servers</h2> <p data-svelte-h="svelte-1897r2a">Disconnecting is straightforward: simply close the tab or window, and you’ll automatically be disconnected from the server.</p> <h2 id="limitations-to-be-aware-of" data-svelte-h="svelte-85zovg">Limitations to be aware of</h2> <ul data-svelte-h="svelte-1jst1u6"><li><strong>Note:</strong> SFTP is not currently supported.</li></ul>`;
