  return `<p data-svelte-h="svelte-3w0y4b">We’re excited to announce that Files v3.7.11 is now available! 🎉 Existing users will receive an update notification in the top right corner of the app, while new users can get started for free from our <a href="/download/">download page</a>. Additionally, you can help support the project by purchasing Files on the <a href="ms-windows-store://pdp/?ProductId=9nghp3dx8hdx&amp;cid=FilesWebsite">Microsoft Store</a> or sponsoring us on <a href="https://github.com/sponsors/yaira2" rel="nofollow">GitHub</a>. Your support is greatly appreciated but entirely optional.</p> <h2 id="whats-new-in-v3711" data-svelte-h="svelte-1n6ofus">What’s new in v3.7.11</h2> <h3 id="added-support-for-renaming-multiple-items-at-the-same-time" data-svelte-h="svelte-1sffkf3">Added support for renaming multiple items at the same time</h3> <p data-svelte-h="svelte-1b7s8lj">We’ve introduced the ability to rename multiple items simultaneously. To get started, select multiple files or folders and click rename. A rename prompt will be displayed where you can enter the new name. All selected items will be renamed accordingly. If there are any duplicate file types, a number will be automatically appended to the file name.</p> <h3 id="improved-ux-for-the-create-new-item-dialog" data-svelte-h="svelte-z8uggq">Improved UX for the “Create new item” dialog</h3> <p data-svelte-h="svelte-1nksmuf">The “Create New Item” dialog now displays the type of item being created, providing more context for your actions. To try this out, click the “New” button on the toolbar and choose an option (e.g., Folder). The dialog will display a confirmation message, such as “Create new folder.”</p> <h3 id="added-an-option-to-copy-the-user-id-from-the-about-page" data-svelte-h="svelte-11h4j1">Added an option to copy the User ID from the About Page</h3> <p data-svelte-h="svelte-1bqaol2">To enhance the debugging experience, we’ve added an option to copy your User ID directly from the About page. Your User ID is a random ID generated when you first install Files and can be used to locate crash reports in Sentry.</p> <h2 id="changes-and-improvements" data-svelte-h="svelte-zx5w86">Changes and Improvements</h2> <ul data-svelte-h="svelte-1w17bof"><li>Reduced minimum window dimensions.</li> <li>Enabled support for typing custom icon path in the properties window.</li> <li>Added Files icon to the process in Windows.</li> <li>Fixed window placement.</li> <li>Fixed an issue where sorting preferences weren’t saved when changed in the Columns Layout.</li> <li>Fixed NullReferenceException in BaseTransferItemAction.ExecuteTransferAsync.</li> <li>Fixed an issue when dragging &amp; dropping items in the sidebar.</li> <li>Fixed an issue where the Start Menu tile didn’t specify the branch.</li> <li>Fixed an issue with navigating photos on Windows 10.</li></ul> <h3 id="conclusion" data-svelte-h="svelte-toxqw4">Conclusion</h3> <p data-svelte-h="svelte-1nk76uf">As always, we appreciate your feedback and suggestions on how we improve Files. You can reach us through our GitHub issues page: <a href="https://github.com/files-community/Files/issues" rel="nofollow">https://github.com/files-community/Files/issues</a>.</p> <p data-svelte-h="svelte-1q4h339">Thank you for using Files! 😊</p> <hr> <p data-svelte-h="svelte-1hrads6">Download Files from our <a href="/download/">download page</a> | Sponsor us on <a href="https://github.com/sponsors/yaira2" rel="nofollow">GitHub</a></p>`;
