import { c as create_ssr_component } from "../../../../../chunks/ssr.js";
const metadata = {
  "title": "Announcing General Availability of Files v3.9",
  "description": "Today, we're excited to announce the release of Files v3.9 for all users.",
  "thumbnail": "/blog-resources/v3-9/Hero.png",
  "date": "1/29/2025",
  "author": "files-community"
};
const Page = create_ssr_component(($$result, $$props, $$bindings, slots) => {
  return `<p data-svelte-h="svelte-18y98ns">I’m excited to announce that Files v3.9 is now available! 🎉 Existing users will receive an update notification in the top right corner of the app, while new users can get started for free from our <a href="/download/">download page</a>. Additionally, you can help support the project by purchasing Files on the <a href="ms-windows-store://pdp/?ProductId=9nghp3dx8hdx&amp;cid=FilesWebsite">Microsoft Store</a> or sponsoring us on <a href="https://github.com/sponsors/yaira2" rel="nofollow">GitHub</a>. Your support is greatly appreciated but entirely optional.</p> <h2 id="changes" data-svelte-h="svelte-1aj9kk0">Changes</h2> <h3 id="introducing-the-cards-view-layout" data-svelte-h="svelte-cvzg7c">Introducing the Cards View layout</h3> <p data-svelte-h="svelte-34afm8">We’ve added a new <strong>Cards View</strong> layout that includes standard metadata like the item thumbnail, name, type, and size, along with context specific properties. For instance, images display their dimensions, media files show their duration, and phones and tablets show their storage details. File tags and sync status for cloud drives are also displayed. This new view replaces the former Tiles View.</p> <figure data-svelte-h="svelte-iq8guc"><img src="/blog-resources/v3-9/CardsViewMedium.png" alt="Medium cards"> <figcaption>Medium Cards</figcaption></figure> <figure data-svelte-h="svelte-1p9jtau"><img src="/blog-resources/v3-9/CardsViewSmall.png" alt="Small cards"> <figcaption>Small Cards</figcaption></figure> <h3 id="improved-contrast-in-the-grid-view-layout" data-svelte-h="svelte-1atz4qy">Improved contrast in the Grid View layout</h3> <p data-svelte-h="svelte-1lucyqf">The <strong>Grid View</strong> layout now features an accent colored border around selected items, improving visibility and contrast.</p> <figure data-svelte-h="svelte-16fxk6v"><img src="/blog-resources/v3-9/GridViewContrast.png" alt="Improved contrast in Grid View layout"> <figcaption>Selected item in the Grid View layout</figcaption></figure> <h3 id="thumbnail-sizes" data-svelte-h="svelte-5cuysz">Thumbnail sizes</h3> <p data-svelte-h="svelte-xn3oga">Icon and thumbnail sizes in the Details View, List View, Columns View, and Cards View are now synchronized with the selected layout size.</p> <figure data-svelte-h="svelte-1a49qfb"><img src="/blog-resources/v3-8-13/DetailsViewIconSizes.png" alt="Large icons in the Details View layout"> <figcaption>Large icons in the Details View layout</figcaption></figure> <h3 id="storage-information-in-the-details-pane" data-svelte-h="svelte-loumx7">Storage information in the Details Pane</h3> <p data-svelte-h="svelte-1qf9ikt">We’ve added a new section to the <strong>Details Pane</strong> that displays drive storage details, giving you quick access to this information.</p> <figure data-svelte-h="svelte-1egv6f1"><img src="/blog-resources/v3-9/DriveDetails.png" alt="Storage information in the Details Pane"> <figcaption>Storage information in the Details Pane</figcaption></figure> <h3 id="custom-shortcut-names" data-svelte-h="svelte-1k4jw18">Custom shortcut names</h3> <p data-svelte-h="svelte-1f0tmdl">You can now assign custom names directly from the <strong>Create New Shortcut</strong> dialog. Previously, shortcut names were automatically assigned and could only be renamed after creation.</p> <figure data-svelte-h="svelte-1b5zfkd"><img src="/blog-resources/v3-9/ShortcutDialog.png" alt="Create shortcut dialog"> <figcaption>Create New Shortcut dialog</figcaption></figure> <h3 id="revamped-release-notes-experience" data-svelte-h="svelte-98drxd">Revamped Release Notes experience</h3> <p data-svelte-h="svelte-1tkymg8">We’ve revamped the <strong>Release Notes</strong> experience. The toolbar button that appears after an update now features a fresh new icon. We’ve also added a new <a href="/docs/customize-settings/actions/">action</a> that lets you access the Release Notes dialog anytime, not just after an update. Finally, the Release Notes dialog now directly displays changelogs from the Files website.</p> <figure data-svelte-h="svelte-1w2v2tr"><img src="/blog-resources/v3-9/ReleaseNotesIcon.png" alt="What's New toolbar button"> <figcaption>Open Release Notes button</figcaption></figure> <h3 id="redesigned-update-files-button" data-svelte-h="svelte-dfj0ld">Redesigned Update Files button</h3> <p data-svelte-h="svelte-17s618j">The <strong>Update Files</strong> toolbar button, which appears when an update is available, has been redesigned with a new accent colored icon. It’s now more noticeable, helping you update Files more easily.</p> <figure data-svelte-h="svelte-1vt4ke"><img src="/blog-resources/v3-9/UpdateFilesIcon.png" alt="Update Files button"> <figcaption>Update Files button</figcaption></figure> <h3 id="dpi-awareness-improvements" data-svelte-h="svelte-1hd33lc">DPI awareness improvements</h3> <p data-svelte-h="svelte-1jhsy83">Icons and thumbnails are now <strong>DPI aware</strong> in the Details View, List View, Columns View, Cards View, the search suggestion flyout, and the Tags Widget, ensuring clarity on all displays.</p> <h3 id="updated-tags-widget" data-svelte-h="svelte-1791pr6">Updated Tags Widget</h3> <p data-svelte-h="svelte-lhlmzq">The <strong>Tags Widget</strong> has been refreshed to align with the overall UI design. We’ve also added a new feature allowing you to apply tags by dragging and dropping files or folders onto tags in the sidebar, making organization even easier.</p> <h3 id="enhanced-support-for-image-files" data-svelte-h="svelte-1j2vdjc">Enhanced support for image files</h3> <p data-svelte-h="svelte-y934n2">You can now rotate JPEG-XR images using the toolbar or right click context menu. Additionally, you can now set JPEG-XR images as your desktop or lockscreen background directly from Files.</p> <h3 id="auto-focus-new-tabs" data-svelte-h="svelte-cpgca3">Auto focus new tabs</h3> <p data-svelte-h="svelte-mme5uj">We’ve added a new setting to automatically switch to newly created tabs. This option is available on the General Settings page.</p> <figure data-svelte-h="svelte-9sq9mk"><img src="/blog-resources/v3-9/AlwaysSwitchTabs.png" alt="Settings dialog"> <figcaption>Auto focus tabs setting</figcaption></figure> <h3 id="improved-properties-window" data-svelte-h="svelte-a80lf6">Improved Properties Window</h3> <p data-svelte-h="svelte-p1whnn">You can now browse for <code>.icl</code> files on the customize page in the <strong>Properties Window</strong>. We’ve also added support for modifying the ‘compress contents’ attribute, and the shortcut page now allows you to adjust the window size for opening shortcut files.</p> <figure data-svelte-h="svelte-4mzi57"><img src="/blog-resources/v3-9/CompressAttribute.png" alt="Compress attribute"> <figcaption>Compress contents attribute</figcaption></figure> <figure data-svelte-h="svelte-1xfe1gx"><img src="/blog-resources/v3-9/ShortcutWindowArgs.png" alt="Shortcut window option"> <figcaption>Shortcut window options</figcaption></figure> <h3 id="new-launch-protocols" data-svelte-h="svelte-ebgk4y">New launch protocols</h3> <p data-svelte-h="svelte-6irss2">We’ve added individual launch protocols for each branch of Files, making it easier to work with different versions.</p> <table data-svelte-h="svelte-13nfnev"><thead><tr><th>Branch</th> <th>Alias</th> <th>URI Scheme</th></tr></thead> <tbody><tr><td>Dev</td> <td>files-dev.exe</td> <td>files-dev:</td></tr> <tr><td>Preview</td> <td>files-preview.exe</td> <td>files-preview:</td></tr> <tr><td>Stable</td> <td>files-stable.exe</td> <td>files-stable:</td></tr></tbody></table> <h3 id="performance-enhancements" data-svelte-h="svelte-yuu2lv">Performance enhancements</h3> <p data-svelte-h="svelte-1ds3l72">Items in the breadcrumb flyout now begin loading as soon as the flyout starts to open, rather than waiting for it to fully open. We’ve also removed the open and close animations from the breadcrumb, back button, and forward button flyouts. Additionally, thumbnails within these flyouts are now loaded lazily, reducing initial load times and improving overall responsiveness.</p> <h3 id="improved-drag--drop-functionality" data-svelte-h="svelte-1gsxkrs">Improved Drag &amp; Drop functionality</h3> <p data-svelte-h="svelte-qp2zi3">The Files window now stays in the background when you drag items to other applications. Additionally, you can now drag files directly from the Recent Files widget into other applications.</p> <h3 id="archive-support" data-svelte-h="svelte-qxrv95">Archive support</h3> <p data-svelte-h="svelte-tzclwq">You can set Files as the default application for opening <code>.gz</code> archives, streamlining access to compressed files. We’ve also updated our 7zip dependencies to leverage the latest improvements.</p> <h3 id="details-view-settings" data-svelte-h="svelte-ea9f1a">Details View settings</h3> <p data-svelte-h="svelte-1zjibh">Toggling columns via the Settings Dialog now updates all existing tabs. Previously, changes only affected new tabs opened after the change.</p> <h3 id="updated-home-page" data-svelte-h="svelte-3cp0w4">Updated Home Page</h3> <p data-svelte-h="svelte-11rt1t7">We’ve improved the Home Page by removing the empty toolbar area, providing a cleaner look.</p> <h3 id="address-bar-keyboard-navigation" data-svelte-h="svelte-13opaih">Address Bar keyboard navigation</h3> <p data-svelte-h="svelte-qxxcfx">We’ve added support for opening breadcrumb flyouts using the <code>Space</code> and <code>Enter</code> keys, improving keyboard navigation.</p> <h3 id="taskbar" data-svelte-h="svelte-egq76s">Taskbar</h3> <p data-svelte-h="svelte-35h8vx">The number of open tabs is no longer displayed in the Windows Taskbar.</p> <h3 id="cloud-drive-support" data-svelte-h="svelte-1k7nd7g">Cloud Drive support</h3> <p data-svelte-h="svelte-fboi2m">Added support for Lucid Link v3.</p> <h2 id="fixes" data-svelte-h="svelte-1bikm6w">Fixes</h2> <h3 id="actions" data-svelte-h="svelte-eo9bei">Actions</h3> <ul data-svelte-h="svelte-cffqp3"><li>Fixed a crash that would occur when using the ‘Storage Sense’ <a href="/docs/customize-settings/actions/">action</a>.</li> <li>Resolved an issue with the ‘Run with PowerShell’ action not working for files with spaces in their names.</li></ul> <h3 id="address-bar" data-svelte-h="svelte-1ysrpdd">Address Bar</h3> <ul data-svelte-h="svelte-wulfr4"><li>Fixed keyboard navigation in the address path bar.</li> <li>Resolved an issue where the address bar flyouts sometimes displayed duplicate items.</li></ul> <h3 id="archives" data-svelte-h="svelte-19m01jg">Archives</h3> <ul data-svelte-h="svelte-1odaqdt"><li>Fixed an issue where the size wasn’t displayed after creating a new archive.</li></ul> <h3 id="cloud-drives" data-svelte-h="svelte-15ziqkv">Cloud Drives</h3> <ul data-svelte-h="svelte-vzqeqb"><li>Addressed various issues with Google Drive support.</li></ul> <h3 id="columns-view" data-svelte-h="svelte-7iadm3">Columns View</h3> <ul data-svelte-h="svelte-tmblir"><li>Fixed an issue that caused unexpected scrolling in the Columns View layout.</li> <li>Resolved a crash that would occur when opening the context menu in the Columns View layout.</li></ul> <h3 id="drag--drop" data-svelte-h="svelte-15r8ugg">Drag &amp; Drop</h3> <ul data-svelte-h="svelte-vnrx9k"><li>Resolved multiple issues with drag &amp; drop support between Files and other applications.</li></ul> <h3 id="git-repos" data-svelte-h="svelte-x0nu9h">Git Repos</h3> <ul data-svelte-h="svelte-163b8nn"><li>Fixed an issue where soft link details were misaligned in Git repositories.</li></ul> <h3 id="group-by-and-sort-by" data-svelte-h="svelte-1j70v9s">Group By and Sort By</h3> <ul data-svelte-h="svelte-83ao3n"><li>Resolved an issue where changing the grouping and sorting preferences would only update the current tab.</li></ul> <h3 id="right-click-context-menu" data-svelte-h="svelte-1rnjih7">Right Click Context Menu</h3> <ul data-svelte-h="svelte-von0ix"><li>Fixed an issue where right-clicking a file would display the option to open in a new tab.</li></ul> <h3 id="startup" data-svelte-h="svelte-17ve9dm">Startup</h3> <ul data-svelte-h="svelte-2y6hyv"><li>Fixed a crash that would occur when starting Files from WSL directories.</li> <li>Resolved startup freeze caused by a heavily populated Recycle Bin.</li> <li>Fixed an issue where the previous session’s tabs were briefly displayed when the startup setting was set to ‘open new tab’.</li> <li>Fixed an issue where launching Files from Terminal didn’t use the correct path.</li></ul> <h3 id="tabs" data-svelte-h="svelte-1cw0o70">Tabs</h3> <ul data-svelte-h="svelte-ec13k4"><li>Resolved an issue where tabs weren’t placed in the correct order when dragged from other windows.</li></ul> <h3 id="tags" data-svelte-h="svelte-2bo5uc">Tags</h3> <ul data-svelte-h="svelte-11yzl3a"><li>Resolved an issue with displaying properties for tagged items.</li> <li>Fixed an issue where tags sometimes displayed with the wrong color.</li></ul> <h3 id="title-bar" data-svelte-h="svelte-3hqkmx">Title Bar</h3> <ul data-svelte-h="svelte-1j6ycnb"><li>Fixed an issue where the title bar drag region was sometimes missing.</li></ul> <h2 id="contributors" data-svelte-h="svelte-1oy258y">Contributors</h2> <p data-svelte-h="svelte-ibb2we">Files is made possible through the collaborative efforts of the open-source community on GitHub. The following developers have contributed to this release:</p> <ul data-svelte-h="svelte-1ae0ut5"><li><a href="https://github.com/0x5bfa"><strong>@0x5bfa</strong></a></li> <li><a href="https://github.com/andreistaravoitau"><strong>@andreistaravoitau</strong></a></li> <li><a href="https://github.com/ashrafmansuri"><strong>@ashrafmansuri</strong></a></li> <li><a href="https://github.com/d2dyno1"><strong>@d2dyno1</strong></a></li> <li><a href="https://github.com/el-ev"><strong>@el-ev</strong></a></li> <li><a href="https://github.com/ferrariofilippo"><strong>@ferrariofilippo</strong></a></li> <li><a href="https://github.com/gave92"><strong>@gave92</strong></a></li> <li><a href="https://github.com/Jack251970"><strong>@Jack251970</strong></a></li> <li><a href="https://github.com/Lamparter"><strong>@Lamparter</strong></a></li> <li><a href="https://github.com/marcofranzen99"><strong>@marcofranzen99</strong></a></li> <li><a href="https://github.com/mdtauk"><strong>@mdtauk</strong></a></li> <li><a href="https://github.com/Roilin-Lab"><strong>@Roilin-Lab</strong></a></li> <li><a href="https://github.com/viruseg"><strong>@viruseg</strong></a></li> <li><a href="https://github.com/wharvex"><strong>@wharvex</strong></a></li> <li><a href="https://github.com/XTorLukas"><strong>@XTorLukas</strong></a></li></ul> <h2 id="conclusion" data-svelte-h="svelte-1cykd0u">Conclusion</h2> <p data-svelte-h="svelte-1a5paqw">As always, we appreciate your feedback and suggestions on how we improve Files. You can reach us on <a href="https://discord.gg/files" rel="nofollow">Discord</a> and on <a href="https://github.com/files-community/Files/" rel="nofollow">GitHub</a>.</p> <p data-svelte-h="svelte-1q4h339">Thank you for using Files! 😊</p> <hr> <p data-svelte-h="svelte-1m9n2bp">Download Files from the <a href="ms-windows-store://pdp/?ProductId=9nghp3dx8hdx&amp;cid=FilesWebsite">Microsoft Store</a> | Sponsor us on <a href="https://github.com/sponsors/yaira2/" rel="nofollow">GitHub</a></p>`;
});
export {
  Page as default,
  metadata
};
