  return `<p data-svelte-h="svelte-tzchu2">In addition to setting a custom <a href="/docs/customize-settings/appearance">background</a> image for the entire window, Files allows you to assign unique background images for different folders. You can also set the image alignment, such as positioning it in a corner of the file area. This feature is perfect for displaying watermarks to easily differentiate between folders.</p> <h2 id="set-a-custom-background-image-for-a-folder" data-svelte-h="svelte-1dkicv5">Set a custom background image for a folder</h2> <ol><li data-svelte-h="svelte-18csoip">Navigate to the folder where you want to set a custom background image.</li> <li data-svelte-h="svelte-mclsy9">Open the <code>desktop.ini</code> file using a text editor (e.g., Notepad).</li> <li>Add the following to the <code data-svelte-h="svelte-drjmhy">desktop.ini</code> file, replacing <code data-svelte-h="svelte-1skxv7d">C:\\Image.png</code> with the actual path to your desired image file:
<pre class="language-ini"><!-- HTML_TAG_START -->${`<code class="language-ini"><span class="token section"><span class="token punctuation">[</span><span class="token section-name selector">FilesApp</span><span class="token punctuation">]</span></span>
<span class="token key attr-name">Files_BackgroundImage</span><span class="token punctuation">=</span><span class="token value attr-value">C:\\Image.png</span></code>`}<!-- HTML_TAG_END --></pre></li> <li data-svelte-h="svelte-19y3zpx">Save the <code>desktop.ini</code> file.</li> <li data-svelte-h="svelte-1xvdp6q">Refresh the folder (you can press F5 or right-click and choose “Refresh”).</li> <li data-svelte-h="svelte-rlk1ic">Enjoy your custom background!</li></ol> <p data-svelte-h="svelte-n7p7pk"><img src="/docs-resources/Folder-Background-Image.png" alt="Folder background image"></p> <h2 id="additional-options" data-svelte-h="svelte-hgh6mv">Additional options</h2> <p data-svelte-h="svelte-1lameu3">You can enhance your background image further by adding the following properties to the <code>desktop.ini</code> file.</p> <h3 id="opacity" data-svelte-h="svelte-12uj9mi">Opacity</h3> <p data-svelte-h="svelte-1gm8ts0">The opacity property accepts values between <code>0.0</code> and <code>1</code>. Lower values make the image more transparent, while higher values make it more opaque. Adjust this setting to achieve the desired effect.</p> <pre class="language-ini"><!-- HTML_TAG_START -->${`<code class="language-ini"><span class="token key attr-name">Files_BackgroundOpacity</span><span class="token punctuation">=</span><span class="token value attr-value">0.4</span></code>`}<!-- HTML_TAG_END --></pre> <h3 id="fit" data-svelte-h="svelte-mel7wm">Fit</h3> <p data-svelte-h="svelte-70z8dd">The <code>Files_BackgroundFit</code> property accepts values between <code>0</code> and <code>3</code>, corresponding to the behaviors listed in the table below.</p> <table data-svelte-h="svelte-1j1ddlf"><thead><tr><th>Value</th> <th>Behavior</th></tr></thead> <tbody><tr><td>0</td> <td>Fill</td></tr> <tr><td>1</td> <td>None</td></tr> <tr><td>2</td> <td>Uniform</td></tr> <tr><td>3</td> <td>UniformToFill</td></tr></tbody></table> <h3 id="vertical-alignment" data-svelte-h="svelte-dqroat">Vertical alignment</h3> <p data-svelte-h="svelte-5idibx">The <code>Files_BackgroundVerticalAlignment</code> property accepts values between <code>0</code> and <code>3</code>, corresponding to the behaviors listed in the table below.</p> <table data-svelte-h="svelte-kahcku"><thead><tr><th>Value</th> <th>Behavior</th></tr></thead> <tbody><tr><td>0</td> <td>Top</td></tr> <tr><td>1</td> <td>Center</td></tr> <tr><td>2</td> <td>Bottom</td></tr> <tr><td>3</td> <td>Stretch</td></tr></tbody></table> <h3 id="horizontal-alignment" data-svelte-h="svelte-6gsfmp">Horizontal alignment</h3> <p data-svelte-h="svelte-2v6cqz">The <code>Files_BackgroundHorizontalAlignment</code> property accepts values between <code>0</code> and <code>3</code>, corresponding to the behaviors listed in the table below.</p> <table data-svelte-h="svelte-rzu0jj"><thead><tr><th>Value</th> <th>Behavior</th></tr></thead> <tbody><tr><td>0</td> <td>Center</td></tr> <tr><td>1</td> <td>Left</td></tr> <tr><td>2</td> <td>Right</td></tr> <tr><td>3</td> <td>Stretch</td></tr></tbody></table>`;
