  return `<h2 id="sync-layout-and-sorting-preferences-across-directories" data-svelte-h="svelte-132h492">Sync layout and sorting preferences across directories</h2> <p data-svelte-h="svelte-1nlwv28">Located on the folders settings page, enabling this option will sync the layout and other folder settings across all directories.  For example, changing the layout in one folder will automatically change the layout for all other folders.</p> <h2 id="layout-type" data-svelte-h="svelte-18amsrb">Layout type</h2> <h3 id="details" data-svelte-h="svelte-90y39w">Details</h3> <p data-svelte-h="svelte-g1v90o">Arranges items using a Data Grid with resizable columns. Support for rearranging columns isn’t supported yet.</p> <h3 id="list" data-svelte-h="svelte-1nmew6c">List</h3> <p data-svelte-h="svelte-1s1bwqj">Displays items in a single column with only the icon and file name.</p> <h3 id="cards" data-svelte-h="svelte-10w39f2">Cards</h3> <p data-svelte-h="svelte-1a06wx0">Displays larger icons and additional information about the items.</p> <h3 id="grid" data-svelte-h="svelte-8g395g">Grid</h3> <p data-svelte-h="svelte-3tb1a3">Grid View allows you to view larger icons.</p> <h3 id="columns" data-svelte-h="svelte-kpbnoy">Columns</h3> <p data-svelte-h="svelte-o35ujn">Displays files and folders in a hierarchical structure, with each level shown in a separate column.</p> <h3 id="adaptive" data-svelte-h="svelte-d0q26k">Adaptive</h3> <p data-svelte-h="svelte-ds6he8">Adaptive automatically chooses the layout based on each folders contents.</p> <h2 id="sorting-and-grouping" data-svelte-h="svelte-18ouxl6">Sorting and grouping</h2> <h3 id="sort-by" data-svelte-h="svelte-15algof">Sort by</h3> <p data-svelte-h="svelte-1au46on">Adjusts how items are sorted</p> <ul data-svelte-h="svelte-9yjo7f"><li>Name</li> <li>Date modified</li> <li>Date created</li> <li>Size</li> <li>Type</li> <li>Tag</li></ul> <h3 id="sort-in-descending-order" data-svelte-h="svelte-2g9d0v">Sort in descending order</h3> <p data-svelte-h="svelte-ws37uk">Sorts items in descending order.</p> <h3 id="sort-priority" data-svelte-h="svelte-shdybx">Sort priority</h3> <ul data-svelte-h="svelte-12zowzu"><li>Sort folders before files</li> <li>Sort files before folders</li> <li>Sort files and folders together</li></ul> <h3 id="group-by" data-svelte-h="svelte-2b7rcv">Group by</h3> <p data-svelte-h="svelte-wgz92u">Adjusts how items are grouped</p> <ul data-svelte-h="svelte-1fmc6we"><li>None</li> <li>Name</li> <li>Date modified</li> <li>Date created</li> <li>Size</li> <li>Type</li> <li>Tag</li></ul> <h3 id="group-in-descending-order" data-svelte-h="svelte-6q3u2l">Group in descending order</h3> <p data-svelte-h="svelte-1dtliv3">Groups items in descending order.</p> <h3 id="group-by-date-unit" data-svelte-h="svelte-2ubknd">Group by date unit</h3> <p data-svelte-h="svelte-1g1ksyc">Adjusts the date unit for group by.</p> <h2 id="details-view" data-svelte-h="svelte-getlpl">Details view</h2> <h3 id="columns-1" data-svelte-h="svelte-soqub4">Columns</h3> <p data-svelte-h="svelte-1bm90is">Support for additional columns is being planned but in the meantime you can toggle the defaults for the following columns:</p> <ul data-svelte-h="svelte-qewjz4"><li>Tags</li> <li>Size</li> <li>Type</li> <li>Date modified</li> <li>Date created</li></ul> <p data-svelte-h="svelte-9vn2xv">To be able to override these options in an individual folder, make sure the option to sync preferences is toggled off.</p>`;
