  return `<p data-svelte-h="svelte-1lpg8y7">We’re excited to announce that Files v3.7 is now available! 🎉 This update brings a host of new features and improvements designed to enhance your experience. Existing users will receive an update notification in the top right corner of the app, while new users can get started for free from our <a href="/download/">download page</a>. Additionally, you can help support the project by purchasing Files on the <a href="ms-windows-store://pdp/?ProductId=9nghp3dx8hdx&amp;cid=FilesWebsite">Microsoft Store</a> or sponsoring us on <a href="https://github.com/sponsors/yaira2" rel="nofollow">GitHub</a>. Your support is greatly appreciated but entirely optional.</p> <p data-svelte-h="svelte-18mrzwg"><strong>TL;DR:</strong> The latest update, Files v3.7, introduces navigation history for back/forward buttons, folder thumbnails in breadcrumb flyouts, an option to hide the System Tray icon, a customizable home button, and the ability to set CPU threads for creating archives. Additionally, there’s experimental support for flattening folders. Continue reading to learn more about these enhancements.</p> <h2 id="whats-new-in-v37" data-svelte-h="svelte-1r08wv2">What’s new in v3.7</h2> <h3 id="added-a-setting-to-hide-the-system-tray-icon" data-svelte-h="svelte-c1bgc8">Added a setting to hide the System Tray icon</h3> <p data-svelte-h="svelte-zl3753">You can now hide the System Tray icon through a new option in the Advanced settings page.</p> <h3 id="focus-existing-tab-when-opening-locations-from-external-process" data-svelte-h="svelte-kfp9hg">Focus existing tab when opening locations from external process</h3> <p data-svelte-h="svelte-9wcebg">When opening a location from an external process, Files will now switch to the existing tab if the location is already open, instead of creating a duplicate tab.</p> <h3 id="view-navigation-history" data-svelte-h="svelte-1qgelts">View navigation history</h3> <p data-svelte-h="svelte-1etx21p">The back and forward toolbar buttons now display a history flyout when right-clicked. On a touchscreen, you can press and hold for a few seconds to display the menu.</p> <figure data-svelte-h="svelte-18yoaov"><img src="/blog-resources/v3-7/NavigationHistory.png" alt="Navigation history flyout"></figure> <h3 id="added-thumbnails-to-the-breadcrumb-flyouts" data-svelte-h="svelte-bzdb3h">Added thumbnails to the breadcrumb flyouts</h3> <p data-svelte-h="svelte-1tiy5w9">We’ve enhanced the breadcrumb flyouts with folder thumbnails, offering better visual navigation and context.</p> <figure data-svelte-h="svelte-1ukdalo"><img src="/blog-resources/v3-7/BreadcrumbFlyout.png" alt="Breadcrumb flyout"></figure> <h3 id="optional-toolbar-button-to-navigate-home" data-svelte-h="svelte-1vzppg7">Optional toolbar button to navigate home</h3> <p data-svelte-h="svelte-1yohoh1">We’ve introduced an optional home button in the toolbar. This feature can be enabled from the Appearance settings page.</p> <figure data-svelte-h="svelte-1ap4qnn"><img src="/blog-resources/v3-7/HomeButton.png" alt="Toolbar home button"></figure> <h3 id="customize-cpu-threads-when-creating-archives" data-svelte-h="svelte-1k30cyb">Customize CPU threads when creating archives</h3> <p data-svelte-h="svelte-1gmmxef">You can now choose the number of CPU threads used when creating archives. This feature allows you to optimize performance and manage system resources more effectively.</p> <figure data-svelte-h="svelte-1rwglre"><img src="/blog-resources/v3-7/CpuThreads.png" alt="Create archive modal"></figure> <h3 id="added-an-action-to-navigate-home" data-svelte-h="svelte-895ymb">Added an action to navigate home</h3> <p data-svelte-h="svelte-1wpxiho">You can now assign a keyboard shortcut to navigate home. This enhancement provides a quick and convenient way to open the home page.</p> <h3 id="experimental-support-for-flattening-folders" data-svelte-h="svelte-15p72d4">Experimental support for flattening folders</h3> <p data-svelte-h="svelte-r3hzgf">This feature allows you to transfer all files from nested folders into the root directory, simplifying your file tree and making file management more efficient. You can enable this option under the Experimental section in Settings &gt; Advanced.</p> <h2 id="changes-and-improvements" data-svelte-h="svelte-zx5w86">Changes and Improvements</h2> <ul data-svelte-h="svelte-12n9qr"><li>Removed the loading animation from the home page.</li> <li>Added an action to open File Explorer’s property window. You can also hold the <code>shift</code> key while pressing the properties button for the same result.</li> <li>Added support for opening the breadcrumb dropdown via keyboard navigation.</li> <li>Added support for Infomaniak kDrive.</li> <li>Added an option to close the active pane from the right-click context menu.</li> <li>Corrected inverted scrolling behavior for icon size adjustment.</li> <li>Fixed an issue where shortcuts couldn’t be created for commands.</li> <li>Fixed an issue where resuming from background didn’t bring Files to the foreground.</li> <li>Fixed an issue where context menus didn’t follow the custom font setting.</li> <li>Fixed an issue where a shortcut to a folder couldn’t be created.</li> <li>Fixed an issue where Google Drive wasn’t displayed when mounted as a folder.</li> <li>Fixed an issue where layout settings didn’t persist across updates.</li> <li>Fixed an issue where folders with file extensions were sometimes treated as files.</li> <li>Fixed an issue where ejecting drives would sometimes fail.</li> <li>Fixed an issue where double-clicking a file would sometimes navigate up.</li> <li>Fixed issues with opening certain file formats.</li></ul> <h3 id="conclusion" data-svelte-h="svelte-toxqw4">Conclusion</h3> <p data-svelte-h="svelte-1nk76uf">As always, we appreciate your feedback and suggestions on how we improve Files. You can reach us through our GitHub issues page: <a href="https://github.com/files-community/Files/issues" rel="nofollow">https://github.com/files-community/Files/issues</a>.</p> <p data-svelte-h="svelte-1q4h339">Thank you for using Files! 😊</p> <hr> <p data-svelte-h="svelte-1hrads6">Download Files from our <a href="/download/">download page</a> | Sponsor us on <a href="https://github.com/sponsors/yaira2" rel="nofollow">GitHub</a></p>`;
