  return `<p data-svelte-h="svelte-7dqoep">It’s just a little over two years since the launch of v2, and the feedback from the community has encouraged us to keep on improving. Our mission with Files is to build the best file manager for Windows, and we’re proud to be building it out in the open so everyone can participate and provide feedback. We’re keenly aware that we wouldn’t have gotten to this point without the support of our dedicated users and the open source community, and we want to extend a thank you to everyone who’s been involved with the project until this point.</p> <p data-svelte-h="svelte-snncin">We’re excited to announce that starting today, v3 is now available. If you already have Files, there should be a notification on the toolbar to install the update. Otherwise, you can download Files for free from our <a href="/download/">download page</a>. You can also purchase Files on the <a href="ms-windows-store://pdp/?ProductId=9nghp3dx8hdx&amp;cid=FilesWebsite">Microsoft Store</a> to help support the project.</p> <p data-svelte-h="svelte-10ocejh"><strong>TL;DR:</strong> Files v3 includes a refreshed design, faster startup performance, a command palette, support for previewing Offices documents, a speed graph for file operations, and more. Continue reading to learn more about these changes.</p> <h2 id="whats-new-in-v3" data-svelte-h="svelte-ql14e0">What’s new in v3</h2> <h3 id="design-updates" data-svelte-h="svelte-1fouj3l">Design updates</h3> <p data-svelte-h="svelte-1c1z37n">When we set out to update the design, we wanted to ensure users would feel at home with the app they love. We were careful not to move the options and commands users are familiar with, whilst modernizing and refreshing core parts of the UI.</p> <ul data-svelte-h="svelte-1jtvzyn"><li>With v3 being one of the larger releases to date, we decided it was a good time to refresh the logo. After exploring a number of ideas and color combinations, we asked the community to help choose a color. It’s probably no surprise, but the community overwhelmingly opted in favor of the familiar yellow color. We also updated the icons for the developer and preview versions of Files (purple &amp; blue) to make it easier to identify the different variants of the app.</li></ul> <figure data-svelte-h="svelte-1ix00oq"><img src="/blog-resources/v3/NewIcon.jpg" alt="Updated Files Icon for all branches"></figure> <ul data-svelte-h="svelte-156bwzt"><li>We redesigned the sidebar from the ground up, removing the icons from headers and the indentations from child items. We also added icons to highlight pinned folders.</li> <li>We rounded the corners on the file area and adjusted the background opacity to provide more distinction between the sidebar.</li></ul> <figure data-svelte-h="svelte-17bgoy"><img src="/blog-resources/v3/Sidebar.jpg" alt="Redesigned sidebar"></figure> <h3 id="improved-experience-for-file-operations" data-svelte-h="svelte-17kaa5e">Improved experience for file operations</h3> <p data-svelte-h="svelte-2mvbr">You can now track the progress and speed of file operations in the Status Center with the new graph feature.</p> <figure data-svelte-h="svelte-1rd32r7"><img src="/blog-resources/v3/StatusCenter.jpg" alt="Updated status center with the speed graph"></figure> <h3 id="rich-previews-for-your-office-documents" data-svelte-h="svelte-1bxoj9h">Rich previews for your Office documents</h3> <p data-svelte-h="svelte-1yppagn">In addition to support for previewing Office documents, we added support for previewing file formats with a registered shell preview handler.</p> <figure data-svelte-h="svelte-7qd0f"><img src="/blog-resources/v3/OfficePreview.jpg" alt="Updated preview pane"></figure> <h3 id="added-a-command-palette" data-svelte-h="svelte-1af9cfp">Added a Command Palette</h3> <p data-svelte-h="svelte-1apzqml">We’re excited to introduce a Command Palette that lets you search and execute commands quickly and easily. You can access the Command Palette by pressing <code>ctrl</code> + <code>shift</code> + <code>p</code> on your keyboard, or by typing <code>&gt;</code> into the address bar. The Command Palette will show you a list of available commands, or you can type in your own query to find the command you need.</p> <figure data-svelte-h="svelte-8yo0dc"><img src="/blog-resources/v3/CommandPalette.jpg" alt="Command Palette"></figure> <h3 id="keep-files-in-the-background" data-svelte-h="svelte-10b1cb8">Keep Files in the background</h3> <p data-svelte-h="svelte-141565i">We added an option to keep Files open in the background when closing the last window. This option is designed to improve the startup performance but can be turned on/off from the advanced settings page. Please be advised the using this option will use system resources even when the app is in the background.</p> <h2 id="changes-and-improvements" data-svelte-h="svelte-zx5w86">Changes and Improvements</h2> <ul data-svelte-h="svelte-1oy8fte"><li>Added support for renaming network drives</li> <li>Theme names are now localized in the settings dialog</li> <li>Keyboard shortcuts are now displayed in the right click menu</li> <li>Added support for opening shortcut files as another user</li> <li>Added support for additional archive formats including <code>gz</code>, <code>mcpack</code>, <code>mcworld</code>, <code>jar</code>, <code>lzh</code>, and <code>appxbundle</code></li> <li>Added support for elevated file operations</li> <li>Folders in special locations that don’t have a directory watcher will now refresh when changes are made</li> <li>Display error message when transferring files that are too large for FAT32</li> <li>Removed the margin when using the compact spacing option</li> <li>Added a “What’s new” popup that can be viewed after an update</li> <li>Files will now display a custom thumbnail when it’s the registered handler for viewing archives.</li> <li>Automatically update items when the date changes</li> <li>Added support for canceling tab tear off by pressing the esc key</li> <li>Added support for copying the paths of multiple items together</li> <li>Improved the design and performance of the startup options in settings</li> <li>Added <code>ctrl</code> + <code>shift</code> + <code>n</code> shortcut to create a new folder</li> <li>Added support for unblocking downloaded folders from the properties window</li> <li>Improved the icon resolution on tabs</li> <li>Added support for Proton Drive</li> <li>Added file operation support for FTP</li> <li>Added support for formatting drives from the properties window</li> <li>Added support to switch between items using arrow keys in the conflicts dialog</li></ul> <h3 id="conclusion" data-svelte-h="svelte-toxqw4">Conclusion</h3> <p data-svelte-h="svelte-1nk76uf">As always, we appreciate your feedback and suggestions on how we improve Files. You can reach us through our GitHub issues page: <a href="https://github.com/files-community/Files/issues" rel="nofollow">https://github.com/files-community/Files/issues</a>.</p> <p data-svelte-h="svelte-1q4h339">Thank you for using Files! 😊</p> <hr> <p data-svelte-h="svelte-1r1a9qf">Download Files from our <a href="/download/">download page</a>.</p>`;
